import React, {
  forwardRef,
  useImperativeHandle,
  useLayoutEffect,
  useCallback,
  useState,
  useMemo,
  useEffect,
  memo,
} from "react";
import NormalMultiSelect from "components/UI/common/NormalMultiSelect";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import { useFieldArray, useWatch, FieldArray } from "react-hook-form";
import IconButton from "@mui/material/IconButton";
import InputBox from "components/UI/common/InputBox/InputBox";
import CommonCheckBox from "components/UI/common/CheckBox/CommonCheckBox";
import RedDeleteIcon from "assets/icons/redDeleteIcon.svg";
import CustomController from "components/UI/common/Controller";
import classes from "./servicetable.module.scss";
import FormErrorMessage from "components/UI/common/ErrorMessage";
import { isRepeated } from "helpers";
import { numberInput } from "helpers/validation";
import { Done } from "@mui/icons-material";

function capitalize(str) {
  if (str) return str.charAt(0).toUpperCase() + str.slice(1).toLowerCase();
  else return "";
}

const TableBody = forwardRef(
  (
    {
      tabValue,
      handleDelete,
      tab,
      setCommonSelectAll,
      setSelectAll,
      errors,
      setValue,
      trigger,
      register,
      control,
      index,
      commonSelectAll,
      watch,
      frequencyList = [],
      retailLongStay,
      retailShortStay,
      corporateShortStay,
      corporateLongStay,
      setFrequencyList,
      saveDraft,
      restMethods,
      showPreview,
      selectAll,
      ...props
    },
    ref
  ) => {
    const [frequencyOption, setFrequencyOption] = useState(frequencyList);

    const { fields, remove, append, insert } = useFieldArray({
      control,
      name: `form.${index}.data`,
    });

    useImperativeHandle(
      ref,
      () => ({
        handleRowDelete,
      }),
      [index]
    );

    const handleRowDelete = (i) => {
      remove(i);
    };

    useEffect(() => {
      handleFrequencyList();
    }, []);

    useEffect(() => {
      const ele = document.getElementById("addNewService");
      if (ele) {
        ele.addEventListener("click", handleAddService);
      }
      return () => {
        if (ele) {
          ele.removeEventListener("click", handleAddService);
        }
      };
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [tab, tabValue]);

    const handleAddService = () => {
      const watchFields = watch(`form.${index}.data`);
      const watchField = watchFields?.[watchFields.length - 1];
      const type =
        watchField?.itemType === "SERVICE" ? "services" : "amenities";
      const roomType = watchField?.roomType.toLowerCase();
      const elem = document.getElementById("addNewService");
      if (elem?.attributes?.selectedFrequency?.value !== "ALL") return;
      if (type === tabValue && roomType === tab) {
        append({
          groupNumber: Math.random() * 9999 | 0,
          name: "",
          frequency: "",
          chargingType: "",
          rate: "",
          ...(watchField?.retailShortStay !== undefined
            ? { retailShortStay: false }
            : {}),
          ...(watchField?.retailLongStay !== undefined
            ? { retailLongStay: false }
            : {}),
          ...(watchField?.corporateShortStay !== undefined
            ? { corporateShortStay: false }
            : {}),
          ...(watchField?.corporateLongStay !== undefined
            ? { corporateLongStay: false }
            : {}),
          itemType: watchField?.itemType,
          propertyId: watchField?.propertyId,
          frequencyId: "",
          roomType: watchField?.roomType,
          roomTypeId: watchField?.roomTypeId,
        });
      }
    };

    const charginTypeData = useMemo(
      () => [
        { value: "PAID", label: "Paid" },
        { value: "COMPLIMENTARY", label: "Complimentary" },
      ],
      []
    );

    const deepClone = (json) => {
      return JSON.parse(JSON.stringify(json));
    };

    const watchfields = watch(`form.${index}.data`);

    const handleAdd = async (i, name, { groupNumber = 0 } = {}) => {
      const isValid = await trigger(`form.${index}.data.${i}.${name}`, {
        shouldFocus: true,
      });
      const watchField = watch(`form.${index}.data.${i}`);
      const key = tabValue === "services" ? "service" : "amenities";
      if (isValid) {
        insert(
          i + 1,
          deepClone({
            groupNumber,
            name: null,
            frequency: "",
            chargingType: "",
            rate: "",
            ...(watchField?.retailShortStay !== undefined
              ? { retailShortStay: false }
              : {}),
            ...(watchField?.retailLongStay !== undefined
              ? { retailLongStay: false }
              : {}),
            ...(watchField?.corporateShortStay !== undefined
              ? { corporateShortStay: false }
              : {}),
            ...(watchField?.corporateLongStay !== undefined
              ? { corporateLongStay: false }
              : {}),
            itemType: watchField?.itemType,
            propertyId: watchField?.propertyId,
            frequencyId: "",
            roomType: watchField?.roomType,
            roomTypeId: watchField?.roomTypeId,
          })
        );
        handleFrequencyList();
      }
      // TODO: Need to check selectAll and header checkbox
      const getActiveTabObj = watch("form").find(field => (field?.bsgp === tab && field?.type === tabValue));
      if (getActiveTabObj) {
        const selectedStays = ["corporateLongStay", "corporateShortStay", "retailLongStay", "retailShortStay"].filter(stay => getActiveTabObj[stay]);
        const newSelectAllData = { ...selectAll };
        if (getActiveTabObj?.data?.length) {
          selectedStays.forEach(stay => {
            newSelectAllData[tabValue][tab][stay] = getActiveTabObj?.data?.every(d => d[stay]);
            const el = document.getElementsByName(stay);
            if (newSelectAllData[tabValue][tab][stay] || getActiveTabObj?.data?.every(d => !d[stay])) {
              if (el?.[0]) el[0].indeterminate = false
            } else {
              if (el?.[0]) el[0].indeterminate = true
            }
          })
          setSelectAll({ ...newSelectAllData })
          setCommonSelectAll(prev => ({
            ...prev,
            [tabValue]: {
              ...prev[tabValue],
              [tab]: selectedStays.every(stay => newSelectAllData[tabValue][tab][stay])
            }
          }))
        }
      }
    };

    const handleFrequencyList = () => {
      /*  const watchFields = watch(`form.${index}.data`);
       let tempArr = []
       const frequencies = watchFields.forEach((field, i) => {
         if(field.name){
           tempArr.
         }
       });
       console.log(frequencies)
       setFrequencyOption((prev) =>
         frequencyList.filter((o) => !frequencies.includes(o.value))
       ); */
    };

    const renderSelectField = ({
      name = "",
      showIcons = true,
      nestedIndex,
      options,
      placeholder,
      item,
      groupNumber,
    }) => {
      return (
        <CustomController
          name={`form.${index}.data.${nestedIndex}.${name}`}
          control={control}
          error={errors.form?.[index]?.data?.[nestedIndex]?.[name]}
          rules={{
            required: saveDraft ? false : true,
            validate: {
              isRepeated: (value, { form }) => {
                if (name === "chargingType") {
                  const data = form?.[index]?.data;
                  if (data?.length) {
                    const groupItemsWithChargeType = data?.filter(
                      (d) =>
                        d.groupNumber === groupNumber && d.chargingType == value
                    );
                    if (groupItemsWithChargeType?.length > 1)
                      return isRepeated(groupItemsWithChargeType, "frequency");
                  }
                }
              },
            },
          }}
          messages={{
            required: "Required",
            isRepeated: "Not valid",
          }}
          render={({ field: { onChange, ref, value }, ...fields }) => {
            return (
              <div className="d-flex  align-items-center">
                <NormalMultiSelect
                  {...fields}
                  selectRef={ref}
                  className={`${classes.reactselect} ${!showIcons && classes.noIcons
                    }`}
                  isAnimateArrow={false}
                  options={options}
                  showValue={watch(`form.${index}.data.${nestedIndex}.${name}`)}
                  name={`form.${index}.data.${nestedIndex}.${name}`}
                  isSearchable={false}
                  placeholder={placeholder || "Select"}
                  menuPosition="fixed"
                  handleChange={(e, arg2) => {
                    onChange(e.target.value);
                    if (name === "frequency") {
                      setValue(
                        `form.${index}.data.${nestedIndex}.frequencyId`,
                        arg2._id
                      );
                      handleFrequencyList();
                    }
                    if (name === "chargingType") {
                      const chargingValue =
                        e.target.value === "COMPLIMENTARY" ? "NA" : "";
                      setValue(
                        `form.${index}.data.${nestedIndex}.rate`,
                        chargingValue
                      );
                    }
                  }}
                  value={
                    watch(`form.${index}.data.${nestedIndex}.${name}`)
                      ? watch(`form.${index}.data.${nestedIndex}.${name}`)
                      : { value: "", label: "" }
                  }
                />
                {showIcons && (
                  <IconButton
                    className={`${classes.iconbtn}`}
                    onClick={() => handleAdd(nestedIndex, name, item)}
                  >
                    <AddCircleOutlineIcon className={classes.circleAdd} />
                  </IconButton>
                )}
              </div>
            );
          }}
        />
      );
    };

    const handleCheckboxChange = (e, onChange, field, name, selectedStays) => {
      const { checked } = e.target;
      onChange({
        type: "change",
        target: {
          name: field.name,
          value: checked,
        },
      });

      const ele = document.getElementsByName(name);
      if (!checked) {
        setCommonSelectAll((prev) => ({
          ...prev,
          [tabValue]: {
            ...prev[tabValue],
            [tab]: false,
          },
        }));
        const isNotCheckedColumn = watchfields.every(
          (field, i) => !field[name]
        );
        if (ele && ele[0]) ele[0].indeterminate = !isNotCheckedColumn;
        setSelectAll((prev) => ({
          ...prev,
          [tabValue]: {
            ...prev[tabValue],
            [tab]: {
              ...prev[tabValue][tab],
              [name]: false,
            },
          },
        }));
      } else {
        const isCheckedColumn = watchfields.every((field, i) => field[name]);
        if (isCheckedColumn) {
          if (ele && ele[0]) ele[0].indeterminate = false;
          setSelectAll((prev) => ({
            ...prev,
            [tabValue]: {
              ...prev[tabValue],
              [tab]: {
                ...prev[tabValue][tab],
                [name]: true,
              },
            },
          }));
        } else {
          if (ele?.[0]) ele[0].indeterminate = true;
        }
        const isAllCheckedColumn = selectedStays?.every((stay) =>
          watchfields.every((field) => field[stay])
        );
        if (isAllCheckedColumn) {
          setCommonSelectAll((prev) => ({
            ...prev,
            [tabValue]: {
              ...prev[tabValue],
              [tab]: true,
            },
          }));
        }
      }
    };

    const renderCheckbox = ({
      name,
      nestedIndex,
      item,
      index,
      showError,
      saveDraft,
      selectedStays,
    }) => {
      const data = watch(`form.${index}.data`);
      let required = showError;
      //  Required true, if all the data stays are unChecked
      if (selectedStays?.length)
        required = selectedStays?.every?.(
          (stay) => !data?.[nestedIndex]?.[stay]
        );
      if (saveDraft) required = false;

      const { onChange, ...field } = register(
        `form.${index}.data.${nestedIndex}.${name}`,
        {
          required,
          validate: {
            isRepeated: () => {
              if (data?.length) {
                const getGroupData = data?.filter(
                  (a) =>
                    a?.groupNumber === item?.groupNumber &&
                    item?.frequency === a?.frequency &&
                    a?.[name] == true
                );
                return !Boolean(getGroupData?.length > 1);
              }
            },
          },
        }
      );
      return (
        <>
          <CommonCheckBox
            name={`form.${index}.data.${nestedIndex}.${name}`}
            customClass={classes.checkbox}
            onChange={(e) => {
              handleCheckboxChange(e, onChange, field, name, selectedStays);

              if (selectedStays?.length) {
                const data = watch(`form.${index}.data`);
                selectedStays.map((stay) => {
                  setTimeout(() => {
                    trigger?.(`form.${index}.data.${nestedIndex}.${stay}`);
                  }, 0);
                });
                if (data?.length) {
                  for (let dataIndex in data) {
                    if (data[dataIndex]?.groupNumber === item?.groupNumber) {
                      selectedStays.map((stay) => {
                        setTimeout(() => {
                          trigger?.(`form.${index}.data.${dataIndex}.${stay}`);
                        }, 0);
                      });
                    }
                  }
                }
              }
            }}
            register={field}
            isChecked={watch(`form.${index}.data.${nestedIndex}.${name}`)}
          />
          <FormErrorMessage
            error={errors.form?.[index]?.data?.[nestedIndex]?.[name]}
            messages={{
              required: "Required",
              isRepeated: "Invalid",
            }}
            position="absolute"
            className={`${classes["mt--4"]}`}
          />
        </>
      );
    };

    const selectedStays = [];
    if (retailShortStay) selectedStays.push(`retailShortStay`);
    if (retailLongStay) selectedStays.push(`retailLongStay`);
    if (corporateShortStay) selectedStays.push(`corporateShortStay`);
    if (corporateLongStay) selectedStays.push(`corporateLongStay`);

    return (
      <>
        {fields &&
          fields.length > 0 &&
          fields.map((item, nestedIndex) => {
            const watchField = watch(`form.${index}.data.${nestedIndex}`);
            return (
              <tr key={item.id}>
                <td className={classes.service}>
                  {item.name !== null && (
                    <>
                      {showPreview ? (
                        <div
                          className={`d-flex align-items-center py-3 p-2 fs-13 fw-500`}
                        >
                          {item?.name}
                        </div>
                      ) : (
                        <>
                          <InputBox
                            placeholder={"Enter service"}
                            inputClass={classes.input}
                            errors={
                              errors.form?.[index]?.data?.[nestedIndex]?.name
                            }
                            onChange={({ target: { name, value } }) => {
                              setValue(name, value, { shouldValidate: true });
                            }}
                            register={register(
                              `form.${index}.data.${nestedIndex}.name`,
                              {
                                required: saveDraft ? false : true,
                                validate: {
                                  dupService: (value) => {
                                    const newData = [...watchfields];
                                    const services = newData
                                      .map(
                                        (o) => o.name && o.name.toLowerCase()
                                      )
                                      .filter((o, n) => n !== nestedIndex);
                                    return value
                                      ? !services.includes(value.toLowerCase())
                                      : true;
                                  },
                                  spaceValidate: (value) => {
                                    return !value.startsWith(" ");
                                  },
                                },
                              }
                            )}
                            value={item.name}
                          />
                          <FormErrorMessage
                            error={
                              errors.form?.[index]?.data?.[nestedIndex]?.name
                            }
                            messages={{
                              required: "Required",
                              dupService: "Value already exist",
                              spaceValidate: "Remove Space",
                            }}
                          />
                        </>
                      )}
                    </>
                  )}
                </td>
                <td style={{ maxWidth: "200px" }}>
                  {showPreview ? (
                    <div
                      className={`d-flex align-items-center py-3 p-2  fs-12 fw-500`}
                    >
                      {item?.frequency}
                    </div>
                  ) : (
                    <>
                      {renderSelectField({
                        nestedIndex,
                        name: "frequency",
                        options: frequencyOption,
                        placeholder: "Add new frequency",
                        item: item,
                        groupNumber: item.groupNumber,
                      })}{" "}
                    </>
                  )}
                </td>
                <td>
                  {showPreview ? (
                    <div
                      className={`d-flex align-items-center py-3 p-2  fs-12 fw-500`}
                    >
                      {capitalize(item?.chargingType || "")}
                    </div>
                  ) : (
                    <>
                      {renderSelectField({
                        nestedIndex,
                        showIcons: false,
                        name: "chargingType",
                        options: charginTypeData,
                        item: item,
                        groupNumber: item.groupNumber,
                      })}{" "}
                    </>
                  )}
                </td>
                <td
                  className={
                    watchField.rate === "NA" ? classes.notapplicable : ""
                  }
                >
                  {watchField.rate === "NA" ? (
                    <div>NA</div>
                  ) : showPreview ? (
                    <div
                      className={`d-flex align-items-center py-3 p-2  fs-12 fw-500`}
                    >
                      {item?.rate}
                    </div>
                  ) : (
                    <>
                      <InputBox
                        placeholder={"Enter rate"}
                        inputClass={classes.input}
                        errors={errors.form?.[index]?.data?.[nestedIndex]?.rate}
                        register={register(
                          `form.${index}.data.${nestedIndex}.rate`,
                          {
                            required: saveDraft ? false : true,
                            validate: {
                              isZero: (value) => {
                                return saveDraft || value !== "0";
                              },
                            },
                          }
                        )}
                        type={"number"}
                        onKeyDown={numberInput}
                        onChange={({ target: { name, value } }) => {
                          setValue(name, value, { shouldValidate: true });
                        }}
                      />
                      <FormErrorMessage
                        error={errors.form?.[index]?.data?.[nestedIndex]?.rate}
                        messages={{
                          required: "Required",
                          isZero: "Invalid",
                        }}
                      />
                    </>
                  )}
                </td>
                {retailShortStay && (
                  <>
                    {" "}
                    {showPreview ? (
                      <td className={`${classes.padding} align-middle`}>
                        <div
                          className={`d-flex text-center align-items-center p-1 fs-12 fw-500 ps-5`}
                        >
                          {Boolean(item?.retailShortStay) ? (
                            <span>
                              <Done />
                            </span>
                          ) : (
                            <div
                              className={`d-flex text-center align-items-center p-1 fs-12 fw-500 ps-5`}
                            ></div>
                          )}
                        </div>
                      </td>
                    ) : (
                      <>
                        <td className={`${classes.padding} align-middle`}>
                          {renderCheckbox({
                            name: "retailShortStay",
                            nestedIndex,
                            item,
                            index,
                            showError: retailShortStay,
                            saveDraft,
                            selectedStays,
                          })}
                        </td>
                      </>
                    )}
                  </>
                )}

                {retailLongStay && (
                  <>
                    {showPreview ? (
                      <td className={`${classes.padding} align-middle`}>
                        <div
                          className={`d-flex text-center align-items-center p-1 fs-12 fw-50 ps-5`}
                        >
                          {Boolean(item?.retailLongStay) ? (
                            <span>
                              <Done />
                            </span>
                          ) : (
                            <div
                              className={`d-flex text-center align-items-center p-1 fs-12 fw-500 ps-5`}
                            > </div>
                          )}
                        </div>
                      </td>
                    ) : (
                      <>
                        <td className={`${classes.padding} align-middle`}>
                          {renderCheckbox({
                            name: "retailLongStay",
                            nestedIndex,
                            item,
                            index,
                            showError: retailLongStay,
                            saveDraft,
                            selectedStays,
                          })}
                        </td>
                      </>
                    )}
                  </>
                )}

                {corporateShortStay && (
                  <>
                    {showPreview ? (
                      <>
                        <td className={`${classes.padding} align-middle`}>
                          <div
                            className={`d-flex text-center align-items-center p-1 fs-12 fw-500 ps-5`}
                          >
                            {Boolean(item?.corporateShortStay) ? (
                              <span>
                                <Done />
                              </span>
                            ) : (
                              <div
                                className={`d-flex text-center align-items-center p-1 fs-12 fw-500 ps-5`}
                              > </div>
                            )}
                          </div>
                        </td>
                      </>
                    ) : (
                      <td className={`${classes.padding} align-middle`}>
                        {renderCheckbox({
                          name: "corporateShortStay",
                          nestedIndex,
                          item,
                          index,
                          showError: corporateShortStay,
                          saveDraft,
                          selectedStays,
                        })}
                      </td>
                    )}
                  </>
                )}

                {corporateLongStay && (
                  <>
                    {" "}
                    {showPreview ? (
                      <>
                        <td className={`${classes.padding} align-middle`}>
                          <div
                            className={`d-flex text-center align-items-center p-1 fs-12 fw-500 ps-5`}
                          >
                            {Boolean(item?.corporateLongStay) ? <Done /> : <div
                              className={`d-flex text-center align-items-center p-1 fs-12 fw-500 ps-5`}
                            > </div>}
                          </div>
                        </td>
                      </>
                    ) : (
                      <>
                        <td className={`${classes.padding} align-middle`}>
                          {renderCheckbox({
                            name: "corporateLongStay",
                            nestedIndex,
                            item,
                            index,
                            showError: corporateLongStay,
                            saveDraft,
                            selectedStays,
                          })}
                        </td>
                      </>
                    )}
                  </>
                )}

                <td className={classes.action}>
                  {nestedIndex > 0 && !showPreview && (
                    <IconButton
                      className={classes.iconbtn}
                      onClick={() => {
                        handleDelete(
                          tabValue,
                          tab,
                          nestedIndex,
                          item._id,
                          index,
                          watchField
                        );
                      }}
                    >
                      <img
                        className={classes.deleteicon}
                        src={RedDeleteIcon}
                        alt="delete icon"
                      />
                    </IconButton>
                  )}
                </td>
              </tr>
            );
          })}
      </>
    );
  }
);

TableBody.displayName = "TableBody";

export default memo(TableBody);
